'use strict';

import format from '@stdlib/error-tools-fmtprodmsg';

// MODULES //

import getDType from '@stdlib/ndarray-base-dtype';

import getShape from '@stdlib/ndarray-base-shape';
import getStrides from '@stdlib/ndarray-base-strides';
import getOffset from '@stdlib/ndarray-base-offset';
import getOrder from '@stdlib/ndarray-base-order';
import getData from '@stdlib/ndarray-base-data-buffer';

// MAIN //

/**
* Transpose a matrix (or a stack of matrices).
*
* @param {ndarray} x - input array
* @param {boolean} writable - boolean indicating whether the returned ndarray should be writable
* @throws {Error} must provide an array with two or more dimensions
* @returns {ndarray} ndarray
*
* @example
* import getData from '@stdlib/ndarray-data-buffer';
* import array from '@stdlib/ndarray-array';
*
* var x = array( [ [ 1, 2, 3 ], [ 4, 5, 6 ] ] );
* // returns <ndarray>[ [ 1, 2, 3 ], [ 4, 5, 6 ] ]
*
* var y = transpose( x, false );
* // returns <ndarray>[ [ 1, 4 ], [ 2, 5 ], [ 3, 6 ] ]
*
* var bool = ( getData( x ) === getData( y ) );
* // returns true
*/
function transpose( x, writable ) {
	var tmp;
	var sh;
	var st;
	var N;

	sh = getShape( x, true );
	N = sh.length;
	if ( N < 2 ) {
		throw new Error( format('0jb0c') );
	}
	st = getStrides( x, true );

	tmp = sh[ N-2 ];
	sh[ N-2 ] = sh[ N-1 ];
	sh[ N-1 ] = tmp;

	tmp = st[ N-2 ];
	st[ N-2 ] = st[ N-1 ];
	st[ N-1 ] = tmp;

	return new x.constructor( getDType( x ), getData( x ), sh, st, getOffset( x ), getOrder( x ), { // eslint-disable-line max-len
		'readonly': !writable
	});
}

// EXPORTS //

export default transpose;
